import React from 'react'
import Alert from '@mui/material/Alert';
import Button from '@mui/material/Button';
import Stack from '@mui/material/Stack';

const SendAlert: React.FC<SendAlertProps> = ({setShowAlert, success}) => {
  
    return (
    <Stack sx={{ width: '80%' }} spacing={2}>
        {success ? 
        <Alert onClose={() => {}}> Wiadomo zostaa przesana do naszych pracownikw. Skontaktujemy si z tob w cigu najbliszych godzin.</Alert>
        : 
        <Alert onClose={() => {}} severity="error"> Wysyanie wiadomoci nie powiodo si. Sprbuj wysa wiadomo ponownie lub uzupeni brakujce pola.</Alert>
        }
        
    </Stack>
  )
}

export default SendAlert;

export interface SendAlertProps{
    setShowAlert: React.Dispatch<React.SetStateAction<boolean>>,
    success: boolean, 
}