import React from "react";
import "./OurOffer.scss";
import TrainingItem from "./TrainingItem";
import { faPersonBiking } from "@fortawesome/free-solid-svg-icons";
import { faMountainSun } from "@fortawesome/free-solid-svg-icons";
import { faCar } from "@fortawesome/free-solid-svg-icons";
import Carousel from "react-multi-carousel";
import { responsive } from "../trainings/Trainings";
import "react-multi-carousel/lib/styles.css";
import {isMobile} from 'react-device-detect';
export interface OurOfferProps {
  setCurrentTab: React.Dispatch<React.SetStateAction<number>>;
}

const OurOffer: React.FC<OurOfferProps> = ({ setCurrentTab }) => {
  console.log(isMobile)
  const trainingItem = [
    {
      textTitle: (
        <span className='ourOffer-title'>
          {" "}
          {/* Szkolenia indywidualne <br /> i grupowe{" "} */}
          SZKOLENIA INDYWIDUALNE <br /> I GRUPOWE{" "}

        </span>
      ),
      icon: faPersonBiking,
      description: <span style={{padding: isMobile ? undefined : '7.5px'}}>Chciaby poprawi swoj technik jazdy {isMobile ? null : <br/>}i poczu si pewniej w terenie? <br/> Na zajciach indywidualnych zrobisz najwikszy progres!</span>,
        // "Chciaby poprawi swoj technik jazdy i poczu si pewniej w terenie? Na zajciach indywidualnych zrobisz najwikszy progres!",
      color: "#B70000",
      srcPath: "szkolenia/1.jpg",
      linkPath: "trainings",
      buttonText: "SPRAWD OFERT",
    },
    {
      textTitle: (
        <span className='ourOffer-title'>
          Klub Rowerowy <br />
          Ride4Fun Academy
        </span>
      ),
      icon: faMountainSun,
      description: <span style={{padding: isMobile ? undefined : '7.5px'}}>Szkka Enduro/Downhill dla dzieci i modziey. Systematyczny trening pod okiem licencjonowanego instruktora to dobra szansa na szybkie postpy {isMobile ? <br/> : null}i rozpoczcie nowych znajomoci!</span>,
        // "Szkka Enduro/Downhill dla dzieci i modziey. Systematyczny trening pod okiem licencjonowanego instruktora to dobra szansa na szybkie postpy i rozpoczcie nowych znajomoci!",
      color: "#B70000",
      srcPath: "item2.jpg",
      linkPath: "trainings",
      buttonText: "DOCZ DO NAS",
    },
    {
      textTitle: (
        <span className='ourOffer-title'>
          Jednodniowe <br /> wyjazdy
        </span>
      ),
      icon: faCar,
      description: <span style={{padding: isMobile ? undefined : '7.5px'}}>Szkolenia jednodniowe to dobra propozycja {isMobile ? null : <br/>}na aktywny dzie pod okiem naszej kadry! Latem odwiedzamy najlepsze miejscwki i Bike Parki {isMobile ? <br/> : null}w Polsce, zim za szkolimy na nartach {isMobile ? null : <br/>}i snowboardzie.</span>,
        // "Szkolenia jednodniowe to dobra propozycja na aktywny dzie pod okiem naszej kadry! Latem odwiedzamy najlepsze miejscwki i Bike Parki w Polsce, zim za szkolimy na nartach i snowboardzie.",
      color: "#B70000",
      srcPath: "item3.jpg",
      linkPath: "trainings",
      buttonText: "ZOBACZ WICEJ",
    },
  ];
  return (
    <div className="ouroffer-main">
      <div className="ouroffer-content">
        {trainingItem.map((item, index) => (
          <TrainingItem
            {...item}
            key={`item.title ${index}`}
            setCurrentTab={setCurrentTab}
            index={index}
          />
        ))}
      </div>
      <div className='ouroffer-content-mobile carousel-media'>
          <Carousel 
            swipeable={true}
            draggable={true}
            showDots={true}
            responsive={responsive}
            infinite={true}
            autoPlay={true}
            autoPlaySpeed={10000}
            keyBoardControl={true}
            transitionDuration={2000}
            containerClass="carousel-container"
            removeArrowOnDeviceType={["tablet", "mobile"]}
            dotListClass="custom-dot-list-style"
            itemClass="carousel-item-padding-40-px"
          >
          {trainingItem.map((item, index) => (
            <div  key={`${item.icon} - carousel`}>
              
              <TrainingItem
                  {...item}
                  key={`${item.buttonText} ${index}-mobile`}
                  setCurrentTab={setCurrentTab}
                  index={index}
                />
            </div>
          
        ))}
        </Carousel>
      </div>
    </div>
  );
};

export default OurOffer;
